* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: center url(img/background.jpg);
  height: 100vh;
  overflow: hidden;
  font-family: 'Roboto', sans-serif;
  -ms-user-select: none; 
  -moz-user-select: none; 
  -webkit-user-select: none; 
  user-select: none; 
}
img { 
  pointer-events: none; 
}
.btn-main {
  font-size: 30px;
	padding: 0 25px;
	outline: none;
	border: 1px solid #fff;
	border-radius: 50px;
	line-height: 46px;
	font-weight: 600;
	text-decoration: none;
  text-align: center;
	color: #891100;
	background-color: #efdac9;
	box-shadow: 0 4px 6px rgb(208 144 96 / 10%), 0 1px 3px rgb(0 0 0 / 8%);
	cursor: pointer;
	user-select: none;
	appearance: none;
	touch-action: manipulation;  
	vertical-align: top;
	transition: box-shadow 0.2s;
}
.btn-main:hover {
	transition: all 0.2s;
	box-shadow: 0 7px 14px rgb(208 144 96 / 100%), 0 3px 6px rgb(0 0 0 / 8%);
}
.btn-main.disabled {
	background-color: rgba(0, 0, 0, 0.2);
  color: #efdac9;
	cursor: not-allowed;
}
.btn-main.disabled:hover {
	box-shadow: none;
}

#root {
  width: 100%;
  height: 100%;
}
.App {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0 10px;
}
.hand, .opponent {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.hand {
  position: relative;
}
.hand__avatar, .opponent__avatar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1em 20px;
}
.hand__avatar h2, .opponent__avatar h2  {
  color: #fff;
  font-size: 50px;
  text-shadow: 0 -2px 0 #000000, 0 -2px 0 #000000, 0 2px 0 #000000, 0 2px 0 #000000, -2px 0 0 #000000, 2px 0 0 #000000, -2px 0 0 #000000, 2px 0 0 #000000, -1px -2px 0 #000000, 1px -2px 0 #000000, -1px 2px 0 #000000, 1px 2px 0 #000000, -2px -1px 0 #000000, 2px -1px 0 #000000, -2px 1px 0 #000000, 2px 1px 0 #000000, -2px -2px 0 #000000, 2px -2px 0 #000000, -2px 2px 0 #000000, 2px 2px 0 #000000, -2px -2px 0 #000000, 2px -2px 0 #000000, -2px 2px 0 #000000, 2px 2px 0 #000000;
  letter-spacing: 3px;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  line-height: 200px;
  text-align: center;
  background: aqua;
}
.hand__cards {
  position: relative;
  display: flex;
  align-items: flex-end;
  width: 70%;
  /* overflow: hidden; */
  padding-left: 250px;
}
.hand__cards .card_item {
  position: absolute;
  left: 0;
  margin-bottom: -200px;
}
.hand__cards .card_item.defuse {
  animation: cardGlow 2s infinite;
}
.hand__cards .card_item:hover {
  margin-bottom: 0;
  z-index: 100;
  animation: cardGlow 2s infinite;
}
.card_item {
  z-index: 10;
}

@keyframes cardGlow {
  0%{
    box-shadow: 0 0 0 #fff;
  }
  50%{
    box-shadow: 0 0 50px #fff;
  }
  100%{
    box-shadow: 0 0 0 #fff;
  }
}

.opponent__avatar h2 {
  background: mistyrose;
}
.opponent__cards {
  position: relative;
  display: flex;
  align-items: flex-end;
  width: 70%;
  /* overflow: hidden; */
  padding-left: 250px;
}
.opponent__cards .card_item {
  position: absolute;
  left: 0;
}
.opponent__cards .card_item:hover {
  margin-bottom: -150px;
  z-index: 100;
}

.board {
  display: flex;
  justify-content: space-between;
}
.board__deck {
  display: flex;
}
.board__deck_cards {
  position: relative;
}
.board__deck_cards img.drawExplode {
  position: absolute;
  top: 0;
}
.board__deck_cards img.drawOpponent {
  position: absolute;
  top: 0;
  animation: drawCardOpponent 6s;
}
.board__deck_cards img.draw {
  position: absolute;
  top: 0;
  animation: drawCardYou 6s;
}

@keyframes drawCardYou {
  0%{
    transform: scale(1);
    top: 0;
    left: 0;

  }
  50%{
    transform: scale(0.5);
    opacity: 1;
  }
  100%{
    transform: scale(0.5);
    opacity: 0;
    left: 50vw;
    top: 25vw;
    display: none;
  }
}
@keyframes drawCardOpponent {
  0%{
    transform: scale(1);
    top: 0;
    left: 0;

  }
  50%{
    transform: scale(0.5);
    opacity: 1;
  }
  100%{
    transform: scale(0.5);
    opacity: 0;
    left: 50vw;
    top: -25vw;
    display: none;
  }
}

.board__deck h2{
  font-size: 30px;
  color: #fff;
  text-align: center;
  background: rgba(0, 0, 0, 0.3);
}
.board__game {
  display: flex;
  align-items: center;
  position: relative;
}
.board__game .lastPlay {
  position: absolute;
  left: 0;
  width: 100%;
}
.board__game_text {
  padding: 50px;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.board__game_text p {
  font-size: 30px;
  color: #fff;
  text-align: center;
}
.board__game_text p.big {
  font-size: 50px;
}
.board__call {
  position: relative;
}
.board__call img {
  max-width: 350px;
}
.board__call p {
  position: absolute;
  font-size: 30px;
  color: #000;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  top: 20%;
}

.modal {
  width: 100vw;
  height: 100vh;
  position: absolute;
  display: none;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 99;
}
.modal.active {
  display: block;
}
.modal .see {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.see__cards {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.see__btn {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}
.see__btn .btn-main {
  min-width: 300px;
  display: none;
}
.see__btn .btn-main.active {
  min-width: 300px;
  display: block;
}
.win {
  position: absolute;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
}
.win.active {
  display: flex;
}
.win h1 {
  font-size: 50px;
  margin-bottom: 20px;
}
.win h2 {
  font-size: 30px;
  margin-bottom: 20px;
}
.hand__rules {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  z-index: 100;
  cursor: pointer;
}
.hand__rules svg {
  display: block;
  max-width: 100%;
  max-height: 100%;
  width: 60px;
  height: 60px;
  color: #891100;
  background-color: #efdac9;
  padding: 8px;
  border-radius: 50px;
}
.hand__rulesScreen {
  position: absolute;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  color: #891100;
  background-color: #efdac9;
  font-size: 20px;
  z-index: 101;
  display: none;
  justify-content: center;
  overflow-y: auto;
}
.hand__rulesScreen.active {
  display: flex;
}
.hand__rulesScreen h1, .hand__rulesScreen h2 {
  margin: 10px 0;
}
.hand__rulesScreen ul {
  list-style: none;
  padding-bottom: 30px;
}
.hand__rulesScreen h3 {
  display: flex;
  align-items: center;
}
.hand__rulesScreen h3 img {
  margin-right: 10px;
}
.hand__rulesScreen img {
  width: 50px;
  height: 50px;
}
.rules_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.rules_header svg {
  width: 40px;
  height: 40px;
  cursor: pointer;
}
.container {
  max-width: 1000px;
  padding: 10px;
}

@media (max-height:1000px) {
  .card_item img, .board__deck_cards img, .see__cards img {
    max-width: calc(100px + (292 - 100) * ( (100vh - 300px) / ( 1000 - 300) ));
  }
  .hand__cards .card_item {
    margin-bottom: calc(-80px + (-200 + 80) * ( (100vh - 300px) / ( 1000 - 300) ));
  }
  .hand__avatar h2, .opponent__avatar h2 {
    font-size: calc(14px + (50 - 14) * ( (100vh - 300px) / ( 1000 - 300) ));
    width: calc(50px + (200 - 50) * ( (100vh - 300px) / ( 1000 - 300) ));
    height: calc(50px + (200 - 50) * ( (100vh - 300px) / ( 1000 - 300) ));
    line-height: calc(50px + (200 - 50) * ( (100vh - 300px) / ( 1000 - 300) ));
  }
  .hand__avatar, .opponent__avatar {
    margin: 0.5em 20px;
  }
  .board__deck h2 {
    font-size: calc(14px + (30 - 14) * ( (100vh - 300px) / ( 1000 - 300) ));
  }
  .board__game_text p {
    font-size: calc(14px + (30 - 14) * ( (100vh - 300px) / ( 1000 - 300) ));
  }
  .board__game_text p.big {
    font-size: calc(20px + (50 - 20) * ( (100vh - 300px) / ( 1000 - 300) ));
  }
  .board__game_text {
    padding: calc(5px + (50 - 5) * ( (100vh - 300px) / ( 1000 - 300) ));
  }
  .board__call img {
    max-width: calc(130px + (292 - 130) * ( (100vh - 300px) / ( 1000 - 300) ));
  }
  .board__call p {
    font-size: calc(14px + (30 - 14) * ( (100vh - 300px) / ( 1000 - 300) ));
    top: 10%;
  }
  .btn-main {
    font-size: calc(14px + (30 - 14) * ( (100vh - 300px) / ( 1000 - 300) ));
    padding: 2px;
    line-height: calc(14px + (46 - 14) * ( (100vh - 300px) / ( 1000 - 300) ));
  }
  .win .btn-main {
    font-size: 30px;
    padding: 0 25px;
    line-height: 46px;
  }
}